.singleTeam[data-theme="corinthians"] {
  background-color: #fff;
  color: var(--base-content-color); }

.singleTeam[data-theme="palmeiras"] {
  background-color: #006436;
  color: white; }

.banner {
  max-width: 217px; }

.link {
  font-size: var(--small-font-size);
  color: inherit;
  font-weight: bold; }

.title {
  font-size: var(--large-font-size);
  margin: var(--large-space) 0;
  text-transform: lowercase; }
